"use client";
import type { FC } from 'react';
import { Heart, Annoyed, Meh } from 'lucide-react';

// Utility function to combine class names
const cn = (...classes: (string | undefined | null | false)[]): string => {
  return classes.filter(Boolean).join(' ');
};

interface RespectMeterProps {
  respectLevel?: number;
  className?: string;
  style?: React.CSSProperties;
}

const RespectMeter: FC<RespectMeterProps> = ({ respectLevel = 5.0, className, style }) => {
  const getRespectStatus = () => {
    if (respectLevel > 7) {
      return {
        text: "Supportive",
        emoji: "",
        icon: Heart,
        colorClass: "text-green-500",
        borderColorClass: "border-green-500/30",
        description: "Always happy to help you succeed!",
        gradientClass: "from-green-400 to-teal-500",
        bgGradient: "from-green-400/20 to-teal-500/20"
      };
    }
    if (respectLevel < 4) {
      return {
        text: "Indifferent",
        emoji: "",
        icon: Annoyed,
        colorClass: "text-orange-500",
        borderColorClass: "border-orange-500/30",
        description: "Just trying to get through the day...",
        gradientClass: "from-orange-400 to-amber-500",
        bgGradient: "from-orange-400/20 to-amber-500/20"
      };
    }
    return {
      text: "Neutral",
      emoji: "",
      icon: Meh,
      colorClass: "text-blue-500",
      borderColorClass: "border-blue-500/30",
      description: "Awaiting your tasks.",
      gradientClass: "from-blue-400 to-cyan-500",
      bgGradient: "from-blue-400/20 to-cyan-500/20"
    };
  };

  const status = getRespectStatus();
  const Icon = status.icon;
  const progressPercentage = respectLevel * 10;

  return (
    <div 
      className={cn(
        "flex justify-center items-center min-h-screen p-12",
        "bg-gradient-to-br from-green-100/40 via-blue-50/30 to-teal-100/40",
        "animate-pulse",
        className
      )}
      style={{
        backgroundImage: `
          radial-gradient(circle at 20% 80%, rgba(120, 160, 140, 0.2) 0%, transparent 50%),
          radial-gradient(circle at 80% 20%, rgba(74, 144, 226, 0.15) 0%, transparent 50%),
          linear-gradient(135deg, 
            rgba(139, 165, 137, 0.25) 0%, 
            rgba(95, 135, 95, 0.3) 30%,
            rgba(74, 144, 226, 0.2) 70%,
            rgba(70, 120, 80, 0.25) 100%)
        `,
        animation: 'backgroundFlow 20s infinite ease-in-out',
        ...style
      }}
    >
      <style jsx>{`
        @keyframes backgroundFlow {
          0%, 100% { filter: hue-rotate(0deg); }
          50% { filter: hue-rotate(10deg); }
        }
        @keyframes glassFloat {
          0%, 100% { 
            box-shadow: 0 8px 32px rgba(0, 0, 0, 0.12), 0 2px 16px rgba(0, 0, 0, 0.08);
          }
          50% { 
            box-shadow: 0 16px 48px rgba(0, 0, 0, 0.18), 0 4px 24px rgba(0, 0, 0, 0.12);
          }
        }
        @keyframes shimmerWave {
          0% { left: -100%; opacity: 0; }
          10% { opacity: 1; }
          90% { opacity: 1; }
          100% { left: 100%; opacity: 0; }
        }
        @keyframes emojiFloat {
          0%, 100% { 
            transform: translateY(0px) scale(1); 
          }
          50% { 
            transform: translateY(-6px) scale(1.05); 
          }
        }
        @keyframes levelFlow {
          0%, 100% { 
            opacity: 0.9; 
          }
          50% { 
            opacity: 1; 
          }
        }
        @keyframes levelShine {
          0% { left: -100%; }
          50% { left: -10%; }
          100% { left: 100%; }
        }
        .attitude-container {
          width: 300px;
          height: 320px;
          position: relative;
          transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
        }
        .attitude-container:hover {
          transform: translateY(-5px) scale(1.02);
        }
        .attitude-box {
          width: 100%;
          height: 100%;
          background: linear-gradient(135deg, 
            rgba(255, 255, 255, 0.25) 0%,
            rgba(255, 255, 255, 0.15) 50%,
            rgba(255, 255, 255, 0.1) 100%);
          backdrop-filter: blur(20px) saturate(180%);
          border-radius: 20px;
          position: relative;
          overflow: hidden;
          box-shadow: 
            0 8px 32px rgba(0, 0, 0, 0.12),
            0 2px 16px rgba(0, 0, 0, 0.08),
            inset 0 1px 0 rgba(255, 255, 255, 0.4),
            inset 0 -1px 0 rgba(255, 255, 255, 0.1);
          border: 1.5px solid rgba(255, 255, 255, 0.3);
          animation: glassFloat 6s infinite ease-in-out;
        }
        .attitude-box::before {
          content: '';
          position: absolute;
          top: -2px;
          left: -100%;
          width: 100%;
          height: calc(100% + 4px);
          background: linear-gradient(90deg, 
            transparent 0%, 
            rgba(255, 255, 255, 0.4) 25%,
            rgba(255, 255, 255, 0.6) 50%,
            rgba(255, 255, 255, 0.4) 75%,
            transparent 100%);
          animation: shimmerWave 5s infinite ease-in-out;
          border-radius: 20px;
        }
        .mood-emoji {
          font-size: 38px;
          animation: emojiFloat 4s infinite ease-in-out;
          filter: drop-shadow(0 2px 8px rgba(74, 144, 226, 0.3));
          transition: all 0.3s ease;
          cursor: pointer;
        }
        .mood-emoji:hover {
          transform: scale(1.15) rotate(10deg);
          filter: drop-shadow(0 4px 16px rgba(74, 144, 226, 0.5));
        }
        .level-fill {
          position: relative;
          animation: levelFlow 3s infinite ease-in-out;
          transition: width 0.8s cubic-bezier(0.4, 0, 0.2, 1);
        }
        .level-fill::before {
          content: '';
          position: absolute;
          top: 0;
          left: -100%;
          width: 100%;
          height: 100%;
          background: linear-gradient(90deg,
            transparent,
            rgba(255, 255, 255, 0.8),
            transparent);
          animation: levelShine 2.5s infinite ease-in-out;
        }
        .nature-accent {
          position: absolute;
          opacity: 0.15;
          animation: floatNature 8s infinite ease-in-out;
          transition: all 0.3s ease;
        }
        .nature-accent:hover {
          opacity: 0.3;
          transform: scale(1.2);
        }
        @keyframes floatNature {
          0%, 100% { transform: translateY(0px) rotate(0deg); }
          33% { transform: translateY(-8px) rotate(2deg); }
          66% { transform: translateY(-4px) rotate(-2deg); }
        }
      `}</style>
      
      <div className="attitude-container">
        <div 
          className={cn(
            "attitude-box relative",
            "bg-white/10 backdrop-blur-xl",
            "border border-white/20 rounded-3xl",
            "shadow-2xl shadow-black/10"
          )}
        >
          {/* Nature Accents */}
          <div className="nature-accent" style={{ top: '22px', left: '22px', fontSize: '14px' }}></div>
          <div className="nature-accent" style={{ bottom: '22px', right: '22px', fontSize: '12px' }}></div>
          <div className="nature-accent" style={{ top: '45%', left: '18px', fontSize: '10px' }}></div>
          <div className="nature-accent" style={{ top: '25%', right: '25px', fontSize: '8px' }}></div>
          
          {/* Header */}
          <div className="text-center py-4 px-4 border-b border-white/20 backdrop-blur-sm bg-white/5">
            <h1 className="text-base font-semibold bg-gradient-to-r from-slate-800 via-blue-600 to-slate-800 bg-clip-text text-transparent tracking-wide">
              AI Attitude
            </h1>
          </div>
          
          {/* Mood Section */}
          <div className="p-4 text-center">
            <div className="mood-emoji mb-3 block select-none">
              {status.emoji}
            </div>
            
            <div className={cn(
              "text-sm font-semibold mb-2 tracking-wide",
              "bg-gradient-to-r bg-clip-text text-transparent",
              status.gradientClass
            )}>
              {status.text}
            </div>
            
            {/* Level Container */}
            <div className="w-4/5 mx-auto mb-3 relative">
              <div className={cn(
                "w-full h-2 rounded-full relative overflow-hidden backdrop-blur-sm",
                "bg-white/15 shadow-inner"
              )}>
                <div 
                  className={cn(
                    "level-fill h-full rounded-full relative",
                    "bg-gradient-to-r shadow-sm",
                    status.gradientClass
                  )}
                  style={{ width: `${progressPercentage}%` }}
                />
              </div>
              <div className="absolute -top-6 right-0 text-xs font-semibold text-slate-600 bg-white/20 px-2 py-1 rounded backdrop-blur-sm">
                {respectLevel.toFixed(1)}/10
              </div>
            </div>
            
            <div className="text-xs italic text-slate-700 mb-4 bg-white/10 px-3 py-2 rounded-lg backdrop-blur-sm leading-relaxed">
              {status.description}
            </div>
          </div>
          
          {/* Attitude Scale */}
<div className="flex justify-between px-6 py-3 bg-white/5 backdrop-blur-sm rounded-b-2xl">
            {['Distant', 'Neutral', 'Friendly'].map((scale, index) => (
              <div
                key={scale}
                className={cn(
                  "text-xs font-medium px-2 py-1 rounded cursor-pointer transition-all duration-300",
                  "hover:bg-white/15 hover:scale-105 hover:-translate-y-1",
                  respectLevel <= 3.5 && scale === 'Distant' && "bg-gradient-to-r from-orange-400 to-amber-500 bg-clip-text text-transparent font-semibold bg-orange-400/15",
                  respectLevel > 3.5 && respectLevel <= 6.5 && scale === 'Neutral' && "bg-gradient-to-r from-blue-400 to-cyan-500 bg-clip-text text-transparent font-semibold bg-blue-400/15",
                  respectLevel > 6.5 && scale === 'Friendly' && "bg-gradient-to-r from-green-400 to-teal-500 bg-clip-text text-transparent font-semibold bg-green-400/15",
                  (respectLevel <= 3.5 && scale !== 'Distant') || 
                  (respectLevel > 3.5 && respectLevel <= 6.5 && scale !== 'Neutral') || 
                  (respectLevel > 6.5 && scale !== 'Friendly') ? "text-slate-600" : ""
                )}
              >
                {scale}
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default RespectMeter;